@import "../../assets/styles/main.scss";

.hero {
    width: 100%;
    height: auto;
    margin: 0;
    padding: 0;
    position: relative;

    @include media("<phone") {
        padding-top: 70px;
    }

    &__slider {
        z-index: 1;
        height: 100vh;
        position: relative;
        overflow: hidden;

        @include media("<phone") {
            height: 40vh;
        }
    }

    &__slide {
        top: 0;
        left: 0;
        opacity: 0;
        height: 100%;
        width: 100%;
        position: absolute;
        background-color: rgba(#000, 0.1);
        visibility: hidden;
        @include trans(opacity 1s ease, visibility 0s ease 1s);

        &.is-selected {
            visibility: visible;
            opacity: 1;
            @include trans(opacity 1s ease);
        }

        &__background {
            margin: 0;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100vh;
            z-index: -1;
            pointer-events: none;
            user-select: none;

            @include media("<phone") {
                height: 40vh;
            }

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center center;
                pointer-events: none;
                user-select: none;
            }
        }
    }

    &__slide__info {
        height: 100%;
        max-width: 90%;
        min-height: 100vh;
        margin-left: auto;
        margin-right: vw(200);
        z-index: 1;
        position: absolute;
        right: 0;
        @include flex-init();

        @include media("<=desktop") {
            max-width: 100%;
            margin-right: vw(100);
        }

        @include media("<phone") {
            margin-right: unset;
            padding: vw(20);
            height: 40vh;
            width: 100vw;
            position: relative;
            min-height: unset;

            &:after{
                left: 0;
                content: '';
                width: 100%;
                height: 50%;
                bottom: 0;
                z-index: 1;
                position: absolute;
                background: rgb(0,0,0);
                background: linear-gradient(0deg, rgba(0,0,0,0.4920343137254902) 0%, rgba(0,0,0,0) 100%);
            }
        }

        &__inner {
            width: 100%;
            display: flex;
            position: relative;
            margin: 43vh 0 0;
            justify-content: flex-end;

            @include media("<phone") {
                flex-direction: column;
                margin: 50% 0 0;
                z-index: 2;
            }

            &__wrapper {
                flex: 0 0 35%
            }
        }
    }

    &__slide__tag {
        color: #FFF;
        width: 100%;
        margin-top: 0;
        font-size: 18px;
        line-height: 25px;
        font-weight: 700;
        position: relative;
        display: inline-block;
        padding-top: 20px;
        @include media("<phone") {
            font-size: vw(18);
        }

        &::before {
            top: 0;
            left: 0;
            content: "";
            width: 100%;
            height: vw(2);
            background-color: $c-primary;
            position: absolute;

            @include media("<phone") {
                display: none;
            }
        }
    }

    &__slide__title {
        //flex: 0 0 250px;
        float: left;
        width: 100%;
        margin-top: 0;
        padding-top: 0;
        font-size: 70px;
        font-weight: 700;
        display: inline-block;
        color: $c-text-secondary;
        position: relative;
        line-height: 58px;

        @include media("<phone") {
            font-size: vw(25);
            width: auto;
            line-height: 1;
            max-width: 230px;
            color: #f2f2f2;
        }
    }

    &__slide__subtitle {
        float: left;
        margin-top: 0;
        flex: 0 0 250px;
        font-weight: 700;
        margin-left: 18px;
        line-height: 22px;
        color: $c-text-primary;
        display: inline-block;

        @include media("<phone") {
            flex: 1 auto;
            margin-left: 0;
            font-size: vw(13);
            color: #f2f2f2;
        }

        .hero__slide__subtitle__content {
            padding: vw(20);
            margin-top: 0;
            background-color: rgba($c-primary, $alpha: 0.5);

            @include media("<phone") {
                margin-left: 0;
                width: 100%;
                padding: 0;
                margin-top: vw(20);
                line-height: 1.2;
                background-color: transparent;
            }
        }
    }

    &__pagination {
        right: 0;
        bottom: 0;
        z-index: 2;
        width: 100vw;
        position: absolute;
        @include flex-init();
        margin-right: vw(200);
        padding-bottom: vw(30);
        justify-content: flex-end;

        @include media("<=desktop") {
            max-width: 100%;
            margin-right: vw(100);
        }

        @include media("<=phone") {
            display: none;
        }

        &__link {
            flex: 0 0 16.67%;
            font-size: 18px;
            line-height: 25px;
            font-weight: 700;
            max-width: vw(200);
            color: rgba($c-text-secondary, $alpha: 0.8);

            &.active {
                color: $c-text-secondary;
                font-weight: 600;
            }

            &__wrapper {
                padding-top: vw(20);
                margin: 0 vw(10);
                position: relative;
            }

            &__wrapper::before {
                content: "";
                top: 0;
                left: 0;
                position: absolute;
                width: 100%;
                height: 1px;
                background-color: $c-primary;
            }
        }
    }
}
