@import "../../assets/styles/main.scss";

.two-column-text {
    background-color: rgba(136, 136, 136, 0.05);
    padding: vw(80) 0;

    @include media("<=phone"){
        padding: 30px 0;
    }

    &__wrapper {
        margin: 0 auto;
        @include flex-init();
        max-width: 1300px;

        @include media("<=tablet") {
            @include flex-direction(column);
            padding: vw(50) vw(20);
        }

        @include media("<=phone") {
            padding: vw(50) vw(20);
        }
    }

    &__column {
        flex: 1 auto;
        padding: 0 20px;

        @include media("<=tablet") {
            max-width: unset;
        }

        @include media("<=phone"){
            padding: 0;
        }

        &:first-child {
            padding-right: 0;
            flex: 0 0 41.666%;


            @include media("<=tablet") {
                padding-right: unset;
            }
        }

        &:last-child {
            padding-left: 8%;

            @include media("<=tablet") {
                padding-left: 0;
            }
        }
    }

    &__subtitle {
        @extend %subtitle;
        color: rgba(25,31,35,0.8);
    }

    &__title {
        margin-top: 0;
        font-weight: 700;

        @include media("<phone") {
            font-size: vw(35);
        }
    }

    &__description {
        // font-size: vw(18);
        margin: 0 0 4vh;
        padding: 0 20px;

        @include media("<=tablet") {
            margin: vw(20) 0;
        }

        @include media("<=phone") {
            padding: 0;
        }
    }

    &__image {
        width: 100%;
        display: block;

        @include media("<=tablet") {
            max-width: unset;
        }

        & img {
            width: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
}
