@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
span {
  display: inline-block;
}

a {
  color: #191f23;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
span,
p,
a {
  margin: 0;
  padding: 0;
}

figure {
  margin: 0 !important;
}

@media (min-width: 1921px) {
  :root {
    --baseSize: 1920;
    --scaler: 1;
  }
}
@media (max-width: 1920px) {
  :root {
    --baseSize: 1920;
    --scaler: 1;
  }
}
@media (max-width: 1440px) {
  :root {
    --baseSize: 1440;
    --scaler: 1;
  }
}
@media (max-width: 767px) {
  :root {
    --baseSize: 768;
    --scaler: 1;
  }
}
@media (max-width: 479px) {
  :root {
    --baseSize: 480;
    --scaler: 1;
  }
}

body {
  font-family: "Roboto", sans-serif;
  margin: 0;
}

.instagram-feed {
  position: relative;
  width: 100%;
}
.instagram-feed .wp-block-group__inner-container {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  padding-top: calc(70 / var(--baseSize) * var(--scaler) * 100vw);
  padding-bottom: calc(70 / var(--baseSize) * var(--scaler) * 100vw);
}
@media (max-width: 1440px) {
  .instagram-feed .wp-block-group__inner-container {
    max-width: calc(1200 / var(--baseSize) * var(--scaler) * 100vw);
  }
}