@import "../../assets/styles/main.scss";

.image-right {
    overflow: hidden;
    position: relative;

    &__wrapper {
        width: 100%;
        max-width: 1510px;
        margin-left: auto;
        position: relative;
        @include flex-init();
        justify-content: space-between;

        @include media("<=tablet") {
            margin: 0 auto;
            max-width: unset;
            @include flex-direction(column);
        }
    }

    &__description {
        padding: 0.75rem 0;
    }

    &__left {
        display: block;
        flex: 0 0 41.6666%;
        position: relative;
        padding-left: 20px;
        padding-top: 3vh !important;

        .image-right__inner-wrapper {
            padding: 0 20% 9vh 0;

            @include media("<=phone") {
                padding-right: 0;
                padding-bottom: 0;
            }
        }


        @include media("<=tablet") {
            width: 100%;
            flex: 0 auto;
            padding: 0 0 50px;
        }

        @include media("<=phone") {
            width: 100%;
            padding-bottom: 20px;
        }
    }

    &__subtitle {
        font-size: 1em;
        height: 2.25rem;
        line-height: 2.1;
        font-weight: 600;
        position: relative;
        padding-left: 1.65rem;
        letter-spacing: -.02em;

        &::before {
            top: 0;
            left: 5px;
            bottom: 0;
            content: "";
            width: 12px;
            height: 2px;
            margin: auto 0;
            position: absolute;
            background-color: currentColor;
            transform: translateY(-50%);
        }
    }

    &__right {
        flex: 1 auto;
        display: block;
        position: relative;

        @include media("<=desktop") {
            width: 50%;
        }

        @include media("<=tablet") {
            width: unset;
        }

        @include media("<=phone") {
            width: 100%;
        }
    }

    &__image {
        height: 100%;
        display: block;
        overflow: hidden;
        position: absolute;
        width: calc(100vw - 41.6666vw);

        @include media(">=1920px") {
            width: 55.7vw;
        }

        @include media("<=tablet") {
            max-height: unset;
            min-width: unset;
            right: unset;
            width: 100vw;
        }

        @include media("<=phone") {
            position: relative;
            margin-left: vw(-20) !important;
        }

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
}
