@import "../../assets/styles/main.scss";

.download-catalog {
    &__wrapper {
        margin: 0 auto;
        @include flex-init();
        @include align-items(center);
        justify-content: space-between;

        @include media("<=tablet") {
            @include flex-direction(column);
            @include align-items(flex-start);
        }
    }

    &__text {
        flex: 0 0 41.6666%;
    }

    &__subtitle {
        // font-size: vw(15);
        font-size: 0.9em;
        font-weight: 500;
    }

    &__title {
        // font-size: vw(46);
        // font-weight: 700;
        margin-top: vw(10);
    }

    &__group {
        flex: 1 auto;
        @include grid-init();
        grid-template-columns: repeat(2, 1fr);
        grid-gap: vw(40);

        @include media("<=tablet") {
            grid-template-columns: 1fr;
        }

        @include media("<=phone") {
            grid-gap: unset;
        }
    }

    &__form {
        flex: 1 auto;

        @include media("<=tablet") {
            width: 100%;
            margin-top: vw(30);
            padding-left: 0 !important;
        }

        @include media("<=phone") {
            margin-top: vw(30);
        }

        .form__input-fields {
            @include flex-init();
            @include justify-content(space-between);

            @include media("<=phone") {
                @include flex-direction(column);
            }

            & input {
                background-color: rgba(150, 144, 162, 0.08);
                padding: 0 vw(16);
                min-height: vw(50);
                height: 100%;
                border-radius: vw(5.6);
                font-size: vw(16.4);
                border: none;
                width: 44%;
                display: inline-block;
                outline: none;

                @include media("<phone") {
                    width: 90%;
                    margin: vw(10) 0;
                }
            }
        }

        .form__submit {
            background-color: $c-tertiary;
            color: $c-text-secondary;
            @include trans(0.3 ease-in-out);
            border-radius: vw(5.4);
            border: none;
            padding: vw(15) vw(30);
            margin-top: vw(20);
            cursor: pointer;
            font-size: vw(18);

            &:hover {
                background-color: $c-secondary;
            }
        }
    }
}
